/* Global Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    height: 100%;
    overflow: hidden;
}

.app-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 0;
    padding-top: 35px;
}

/* language selection Styles */
.language-toggle-container {
    display: flex;
}

.language-toggle-button {
    background-color: #ccc;
    border: none;
    padding: 8px 12px;
    margin-right: 8px;
    cursor: pointer;
}

.language-toggle-button.active {
    background-color: #007bff;
    color: #fff;
}

/* Container Styles */
.differ-container {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
    overflow: hidden;
}

/* Header Styles */
.differ-header {
    position: sticky;
    top: 0;
    display: flex;
    background-color: #fff;
    z-index: 1;
}

.differ-title-language {
    flex: 1;
    padding: 10px;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    text-align: center;
    margin-bottom: 25px;
}

/* Content Styles */
.differ-content {
    flex: 1;
    overflow-y: auto;
    padding: 10px;
}

/* Syntax Row Styles */
.syntax-row {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    background-color: rgb(242, 249, 250);
    padding: 10px;
}

.differ-content h2 {
    border-bottom: 2px solid #ccc; /* Add a border-bottom style */
    padding-bottom: 5px;
    margin-bottom: 20px;
}

.syntax-row h3 {
    flex-basis: 100%;
    margin-bottom: 10px;
    text-align: left;
}

/* Syntax Column Styles */
.syntax-column {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

/* Syntax Box Styles */
.syntax-box {
    flex: 1;
    /* border: 1px solid #ccc; */
    padding: 10px;
    margin: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    word-break: break-word;
}

.syntax-box pre {
    background-color: #f9f9f9;
    padding: 10px;
    border-radius: 5px;
    white-space: pre-wrap;
    word-wrap: break-word;
    overflow-x: auto;
}

